"use client";

import { useEffect, useState } from 'react';
import { useSearchParams } from 'next/navigation';
import DashboardLayout from '@/components/layouts/DashboardLayout';
import ApplicationForm from '@/components/ats/ApplicationForm';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';

interface Job {
  id: string;
  title: string;
  shortDescription: string;
  status: string;
  location: string;
  type: string;
}

export default function ApplyPage() {
  const searchParams = useSearchParams();
  const jobIdFromUrl = searchParams.get('jobId');
  
  const [jobs, setJobs] = useState<Job[]>([]);
  const [selectedJobId, setSelectedJobId] = useState<string>(jobIdFromUrl || '');
  const [selectedJob, setSelectedJob] = useState<Job | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    fetchJobs();
  }, []);

  useEffect(() => {
    if (selectedJobId && jobs.length > 0) {
      const job = jobs.find(j => j.id === selectedJobId);
      setSelectedJob(job || null);
    } else {
      setSelectedJob(null);
    }
  }, [selectedJobId, jobs]);

  const fetchJobs = async () => {
    try {
      setLoading(true);
      const response = await fetch('/api/jobs');
      
      if (!response.ok) {
        throw new Error('Failed to fetch jobs');
      }
      
      const data = await response.json();
      // Only show open jobs for application
      const openJobs = data.filter((job: Job) => job.status.toLowerCase() === 'open');
      setJobs(openJobs);
      setError(null);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred');
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <DashboardLayout>
        <div className="flex items-center justify-center min-h-64">
          <div className="text-center">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
            <p className="text-muted-foreground">Loading available positions...</p>
          </div>
        </div>
      </DashboardLayout>
    );
  }

  return (
    <DashboardLayout>
      <div className="max-w-4xl mx-auto space-y-8">
        <div className="text-center space-y-4">
          <h1 className="text-3xl font-bold">Apply for a Position</h1>
          <p className="text-muted-foreground text-lg">
            Join our team by submitting your application for one of our open positions
          </p>
        </div>

        {error && (
          <Alert className="border-red-200 bg-red-50">
            <AlertDescription className="text-red-800">
              {error}
            </AlertDescription>
          </Alert>
        )}

        {jobs.length === 0 ? (
          <Card className="text-center py-12">
            <CardContent>
              <div className="space-y-4">
                <h3 className="text-lg font-medium">No Open Positions</h3>
                <p className="text-muted-foreground">
                  There are currently no open positions available for application.
                  Please check back later or contact us directly.
                </p>
                <Button onClick={() => window.location.href = '/jobs'}>
                  View All Job Postings
                </Button>
              </div>
            </CardContent>
          </Card>
        ) : (
          <div className="space-y-6">
            {/* Job Selection */}
            <Card>
              <CardHeader>
                <CardTitle>Select Position</CardTitle>
                <CardDescription>
                  Choose the position you'd like to apply for
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Select value={selectedJobId} onValueChange={setSelectedJobId}>
                  <SelectTrigger className="w-full">
                    <SelectValue placeholder="Select a position to apply for..." />
                  </SelectTrigger>
                  <SelectContent>
                    {jobs.map((job) => (
                      <SelectItem key={job.id} value={job.id}>
                        <div className="flex flex-col items-start">
                          <span className="font-medium">{job.title}</span>
                          <span className="text-sm text-muted-foreground">
                            {job.location} • {job.type}
                          </span>
                        </div>
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>

                {selectedJob && (
                  <div className="mt-4 p-4 bg-muted rounded-lg">
                    <h4 className="font-medium mb-2">{selectedJob.title}</h4>
                    <p className="text-sm text-muted-foreground mb-2">
                      {selectedJob.shortDescription}
                    </p>
                    <div className="flex items-center gap-4 text-xs text-muted-foreground">
                      <span>{selectedJob.location}</span>
                      <span>•</span>
                      <span>{selectedJob.type}</span>
                    </div>
                  </div>
                )}
              </CardContent>
            </Card>

            {/* Application Form */}
            {selectedJobId && selectedJob && (
              <ApplicationForm 
                jobId={selectedJobId} 
                jobTitle={selectedJob.title}
              />
            )}

            {!selectedJobId && (
              <Card className="text-center py-8">
                <CardContent>
                  <p className="text-muted-foreground">
                    Please select a position above to begin your application
                  </p>
                </CardContent>
              </Card>
            )}
          </div>
        )}

        {/* Additional Information */}
        <Card>
          <CardHeader>
            <CardTitle>Application Tips</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div>
              <h4 className="font-medium mb-2">Before You Apply</h4>
              <ul className="text-sm text-muted-foreground space-y-1">
                <li>• Review the job requirements carefully</li>
                <li>• Prepare your most recent resume (PDF, DOC, or DOCX format)</li>
                <li>• Write a compelling cover letter that highlights your relevant experience</li>
                <li>• Double-check all your contact information</li>
              </ul>
            </div>
            <div>
              <h4 className="font-medium mb-2">What Happens Next</h4>
              <ul className="text-sm text-muted-foreground space-y-1">
                <li>• We'll review your application within 3-5 business days</li>
                <li>• If you're a good fit, we'll contact you to schedule an interview</li>
                <li>• You'll receive email updates about your application status</li>
                <li>• Feel free to reach out if you have any questions</li>
              </ul>
            </div>
          </CardContent>
        </Card>
      </div>
    </DashboardLayout>
  );
}
